
















































import { Component, Vue } from "vue-property-decorator";
import DataService from "../services/DataService";

@Component
export default class AddKicker extends Vue {
  kicker: any = {
    name: null,
    email: null,
    password: null,
  };

  submitted: boolean = false;

  saveKicker() {
    var data = {
      name: this.kicker.name,
      email: this.kicker.email,
      password: this.kicker.password,
    };

    DataService.create(data)
      .then((response) => {
        console.log(response.data);
        this.submitted = true;
      })
      .catch((e) => {
        console.log(e);
      });
  }

  newKicker() {
    this.submitted = false;
    this.kicker = {};
  }
}
